@use '../base/mixins' as mx;
@use '../base/variables' as v;

.video{
    position: relative;//para encimar las letras en el video
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay{
        position: absolute; 
        background: rgb(0,78,21);
        background: linear-gradient(270deg, rgba(0,78,21,0.5) 0%, rgba(192,252,46,0.3) 100%);
        width: 100%;
        height: 100%;
       display: flex;
       flex-direction: column;
       justify-content: center;
    }
    video{
        
        display: block;
        @include mx.desktop{
            width: 100%;    
        }
    }
}
.contenido-video{
    z-index: 10;
    @include mx.phone{
        margin-left:0;
    }
    @include mx.tablet{
        margin-left: 150px;
    }
    h2{
        @include mx.phone{
            font-size: 7rem;
        }
        color: v.$blanco;
        font-size: 4rem;
        margin: 0;
    }
    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        @include mx.phone{
            font-size: 2.4rem;
        }
        font-size: .5rem;
        text-align: center;
        margin: 0;
    }
}