@use '../base/variables' as v;
@use '../base/mixins' as mx;
.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
    }
    .contenedor-boletos{
        @include mx.tablet{
            @include mx.grid(2, 5rem);
        }
    }
}
.pase{
    .nombre-pase{
        text-align: center;
        font-size: 2.4rem;
        color: v.$blanco;
    }
    &-header,
    &-footer {
        padding: .5rem;
       p{
        text-align: center;
        color: v.$blanco;
        margin: 0;
       } 
    }

    &-header{//en lugar de .pase-header
        background-color: v.$rosa;
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-body{//en lugar de .pase-body
        background-color: v.$blanco;
        ul{
            @include mx.resetear-lista;
            li,
            :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #1e1e11;
            }
        }
    }
    &-footer{//en lugar de .pase-footer
        background-color: v.$morado;
        p{
            font-size: 2.4rem;
            font-weight: 700;
        }
    }

}