@use '../base/variables' as v;

@mixin phone {
    @media (min-width: v.$telefono){
        @content;// para poderlo usar en un bloque de codigo
    }
}
@mixin tablet {
    @media (min-width: v.$tablet){
        @content;// para poderlo usar en un bloque de codigo
    }
}
@mixin desktop {
    @media (min-width: v.$desktop){
        @content;// para poderlo usar en un bloque de codigo
    }
}
@mixin desktopxl {
    @media (min-width: v.$desktopxl){
        @content;// para poderlo usar en un bloque de codigo
    }
}
@mixin grid($columnas,$gap) {
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    gap: $gap;
}
@mixin heading {
    text-transform: uppercase;
    font-size: 2.4rem;
    color: black;
    letter-spacing: 2px;
    border-radius: 0rem;
}
@mixin heading1($color: blue, $border-radius: false) {// mixins inteligentes con argumentos
    text-transform: uppercase;
    font-size: 2.4rem;
    color: $color;
    letter-spacing: 2px;
    background-color: black;
    @if $border-radius{
        border-radius: 1rem;
    }
}
@mixin headingMorado {
    text-transform: uppercase;
    font-size: 2.4rem;
    color: v.$morado;
    letter-spacing: 2px;
}
@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}