@use '../base/variables' as v;
@use '../base/mixins' as mx;
.header{
 background-color: v.$verde;
 //para la barra fija 
 @include mx.tablet{
    &.fijo{
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        box-shadow: .1rem .2rem .3rem #666;
     }
    
 }
 

 h1{
    color: v.$blanco;
    text-align: center;
    @include mx.tablet{
        margin:0;
    }
 }
 .contenido-header{
    @include mx.tablet{
        display:flex;
        justify-content: space-between;
        padding: 2rem;
        align-items: center;
    }
 }
}

.navegacion-principal{
    @include mx.phone{
        
    }
    @include mx.tablet{
        display: flex;
        margin-right: 1rem;
    }
    @include mx.desktop{
        
    }
    @include mx.desktopxl{
        
    }
    
    a{
        text-align: center;
        display: block;
        margin-bottom: 1rem;
        margin-right: 1rem;
        @include mx.tablet{
            margin-top: 0;
        }
        color: v.$blanco;
        font-size: 2.4rem;
        //hover se usa para dar color a los elementos al pasar el raton por encima 
        &:hover{ //se le agrega $ para que el :hover quede pegado al selector en el css
            color: v.$amarillo;
        }
    }
}