@use '../base/mixins' as mx;
@use '../base/variables' as v;


.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}
.galeria-imagenes{
    @include mx.resetear-lista;
    @include mx.grid(2, 2rem);
    @include mx.phone{
        @include mx.grid(3, 2rem);
    }
    @include mx.tablet{
        @include mx.grid(4, 2rem);
    }
    @include mx.desktop{
        @include mx.grid(6, 2rem);
    }
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;
    display:grid;
    place-items: center;

    background-color: rgba(0,0,0,.5);/*rgb(0 0 0/ .5)*/
    picture{
        display: block;
        max-width: 80rem;
    }
}
.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;
    
    &:hover{
        cursor: pointer;
    }
}