@use '../base/mixins' as mx;
@use '../base/variables' as v;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    h3, p{
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}
.escenarios-contenedor{
    @include mx.tablet{
        @include mx.grid(2,2rem);
        grid-area: 10%;
    }
    p{
        color: v.$negro;
    }
    .escenario{
        padding-top: 2rem;
         p{   //para agregar las propiedades antes del icono
            &::after{
                content: '';
                display: block;
                width: 20rem;
                height: 10rem;
                background-size: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem auto 0 auto;
            }
        }
    }
}
//se agrega el icono
.edm .nombre-escenario:after{
    
    background-image: url('../img/icono_edm.png');
}
.rock .nombre-escenario:after{
    
    background-image: url('../img/icono_rock.png');
}
.calendario{
    @include mx.resetear-lista;
    li{
        padding: 2.5rem;
        font-weight: 850;
        span {
            font-weight: 400;
        }
    }
}

.bg-amarillo{
    background-color: v.$amarillo;
    li:nth-child(odd){ //para selecionar impares
        background-color: darken(v.$amarillo, 20%);
    }
}
.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){ //para selecionar impares
        background-color: darken(v.$verde, 10%);
    }
}