@use '../base/mixins' as mx;
@use '../base/variables' as v;

.sobre-festival{
    padding: 5rem 0;
    @include mx.tablet{
        @include mx.grid(2,5rem);
    }
}
.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include mx.tablet{
        marging-top: 0;
    }
    h2{
        margin-bottom: 2rem;
    }
    .fecha{
        color: v.$rosa;
        font-weight: 700;
    }
}